@include font('Gilroy', "Gilroy-Heavy", 900 );
@include font('Gilroy', "Gilroy-ExtraBold", 800 );
@include font('Gilroy', "Gilroy-Bold", 700 );
@include font('Gilroy', "Gilroy-SemiBold", 600 );
@include font('Gilroy', "Gilroy-Medium", 500 );
@include font('Gilroy', "Gilroy-Regular", 400 );
@include font('Gilroy', "Gilroy-Light", 300 );

html {
	@include r(991) {
		font-size: 15px; }
	@include r(767) {
		font-size: 14px; } }

body {
	font-family: "Gilroy";
	font-weight: 400;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.4;
	color: #151e2e; }

.btn {
	font-family: "Gilroy";
	font-weight: 500;
	border-radius: 3px;
	@include tr(75);
	padding: .625rem .75rem; }

.btn-link {
	position: relative;
	@include css-lock(18, 24, 575, 991);
	@include tr(25);
	&:before {
		position: absolute;
		content: '';
		left: $input-padding-x;
		right: $input-padding-x;
		height: 2px;
		bottom: 0.75rem;
		background-color: $primary;
		transition: $transition-base; }
	&:hover {
		text-decoration: none; }
	&:hover:before {
		background-color: $link-hover-color; } }



.btn-sm {
	padding: .375rem 0.25rem;
	@include fs(14);
	font-weight: 400;
	@include tr(25); }

.bg-primary,
.bg-secondary {
	color: #fff; }

.sect {
	padding: 55px 0;
	@include r(1199) {
		padding: 50px 0; }
	@include r(991) {
		padding: 45px 0; }
	@include r(767) {
		padding: 40px 0; }
	@include r(575) {
		padding: 35px 0; } }

.sect-title {
	@include css-lock(24, 36, 575, 991);
	@include tr(25);
	margin-bottom: 2rem;
	font-weight: 600;
	text-align: center; }

.form-control {
	text-align: center;
	background-color: transparent;
	transition: $transition-base;
	border-radius: 3px;
	@include placeholder {
		@include fs(18);
		font-weight: normal;
		@include tr(75); } }

a {
	color: $primary;
	text-decoration: none;
	&:hover {
		text-decoration: underline; } }

img {
	display: inline-block;
	height: auto;
	max-width: 100%; }


.main-header {
	.container-fluid {
		max-width: map-get($container-max-widths, "xl"); } }

.secondary-nav {
	padding: 1rem 0; }

.secondary-nav-list {
	display: flex;
	justify-content: space-around;
	align-items: center;
	flex-wrap: wrap;
	&__item a {
		display: inline-block;
		padding: 0.5rem .75rem;
		color: #fff;
		font-weight: bold;
		@include tr(250);
		@include fs(12); }
	&__item a:hover {
		text-decoration: none; } }

.main-header-content {
	padding: 0.75rem 0; }


.logo {
	text-align: center;
	@include tr(25);
	&__text {
		font-weight: bold;
		@include css-lock(36, 44, 575, 991);
		color: $primary;
		line-height: 1;
		white-space: nowrap;
		font-weight: 800; }
	&__sub {
		@include css-lock(16, 20, 575, 991);
		font-weight: normal; }
	&__sub .text-primary {
		text-decoration: underline;
		text-decoration-style: dotted; } }

.header-gallery {
	margin: 0 0.625rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	&__item {
		font-size: 0; } }

.info {
	@include tr(25);
	&__item {
		display: block;
		@include fs(16);
		margin-bottom: .175rem;
		@include r(767) {
			@include fs(16); } }
	&__item-link {}
	&__item-link--mail {
		color: #313131;
		font-weight: 500; }
	&__item-link--phone {
		font-weight: 600; } }


.primary-nav {
	border-top: 1px solid #f4f3f3; }

.primary-nav-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	&__item {
		padding: 1rem 0; }
	&__item.active {
		position: relative; }
	&__item.active:before {
		position: absolute;
		content: '';
		left: 0.75rem;
		right: 0.75rem;
		bottom: 0;
		height: 2px;
		background-color: $primary; }
	&__item.active a {
		color: $primary;
		position: relative; }
	&__item a {
		@include fs(16);
		@include tr(25);
		display: inline-block;
		padding: 0.5rem 0.75rem;
		font-weight: 400;
		color: #151e2e;
		transition: $transition-base; }
	&__item a:hover {
		color: $primary;
		text-decoration: none; } }

.hero {
	position: relative;
	background-image: url(../img/hero-bg.jpg);
	background-color: #0c0f1e;
	padding-top: 210px;
	padding-bottom: 270px;
	@include cover;
	color: #fff;
	text-align: center;
	@include r(1199) {
		padding-top: 180px;
		padding-bottom: 230px; }
	@include r(991) {
		padding-top: 150px;
		padding-bottom: 180px; }
	@include r(767) {
		padding-top: 120px;
		padding-bottom: 130px; }
	@include r(575) {
		padding-top: 80px;
		padding-bottom: 80px; }
	.container {
		position: relative;
		z-index: 5; }
	&:before {
		z-index: 1;
		position: absolute;
		content: "";
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		background-color: rgb(6, 6, 6);
		opacity: 0.58; }
	&__title {
		color: #ffffff;
		text-align: center;
		font-weight: 900;
		@include css-lock(22, 48, 575, 1400);
		position: relative;
		padding-bottom: 1.125rem;
		margin-bottom: 1.5rem;
		@include tr(75);
		&:before {
			position: absolute;
			content: '';
			background-color: $primary;
			max-width: 136px;
			margin: 0 auto;
			height: 3px;
			left: 0;
			right: 0;
			bottom: 0; } }
	&__title-sub {
		display: block;
		font-weight: 500;
		@include css-lock(18, 30, 575, 991); }
	&__text {
		@include css-lock(17, 22, 575, 991);
		font-weight: 300;
		line-height: 1.2;
		@include tr(75);
		margin-bottom: 2rem; } }

.feature {
	background-image: url(../img/feature-bg.png);
	background-repeat: no-repeat;
	background-position: 100% -100px; }

.feature-item {
	margin-bottom: 1.5rem;
	&__icon {
		margin-bottom: 1.25rem; }
	&__title {
		font-weight: 700;
		@include css-lock(16, 18, 575, 991);
		padding-bottom: 0.75rem;
		position: relative;
		margin-bottom: 0.625rem;
		&:before {
			position: absolute;
			content: '';
			width: 57px;
			height: 1px;
			background-color: $primary;
			left: 0;
			bottom: 0; } }
	&__text {
		@include fs(14);
		@include tr(25);
		color: #9dacc2;
		line-height: (26 / 14); } }

.banner {
	background-color: #151e2e;
	color: #fff;
	padding: 2.375rem 0;
	@include r(767) {
		padding: 1.5rem 0; }
	&__text {
		font-weight: 300;
		@include css-lock(18, 30, 575, 991);
		@include tr(25);
		color: #fff;
		text-align: center; }
	&__text a {
		color: #fff;
		font-weight: 500;
		display: inline-block;
		border-bottom: 2px dashed $primary;
		transition: $transition-base; }
	&__text a:hover {
		text-decoration: none;
		border-color: #fff; } }

.price {
	padding: 2rem 0; }


.action {
	background-image: url(../img/action-bg.jpg);
	@include cover;
	text-align: center;
	position: relative;
	&:before {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		content: '';
		background-color: rgb(25, 36, 55);
		opacity: 0.91;
		z-index: 2; }
	.container {
		position: relative;
		z-index: 10; }
	&__text {
		text-align: center;
		@include css-lock(16, 18, 575, 991);
		font-weight: 400;
		margin-bottom: 2.625rem;
		@include tr(25); }
	&__text .text-uppercase {
		@include css-lock(20, 30, 575, 991);
		line-height: 1;
		font-weight: 800; } }

.why-item {
	text-align: center;
	margin-bottom: 2rem;
	&__num {
		@include css-lock(50, 72, 575, 991);
		color: $primary;
		font-weight: 300; }
	&__title {
		font-weight: 300;
		margin-left: -1px;
		margin-right: -1px;
		@include css-lock(19, 22, 575, 991);
		@include tr(25);
		padding-bottom: 1rem;
		position: relative;
		margin-bottom: 0.5rem; }
	&__title:before {
		position: absolute;
		content: '';
		left: 0;
		right: 0;
		margin: auto;
		bottom: 0;
		height: 2px;
		max-width: 30px;
		background-color: $primary; }
	&__text {
		@include css-lock(14, 16, 575, 991);
		line-height: (29/16); } }

.sect-alert {
	background-image: url("../img/sect-alert-bg.png");
	background-repeat: no-repeat;
	background-position: center center;
	overflow: hidden;
	padding: 30px 0;
	&__img {
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
		@include r(767) {
			left: -200px;
			opacity: .2; } }
	&__img img {
		max-width: unset; }
	&__text {
		@include css-lock(20, 24, 575, 991);
		@include tr(25); } }

.delivery {
	&__text {
		font-weight: 300;
		@include tr(25);
		@include css-lock(14, 16, 575, 991);
		margin-bottom: 2rem;
		line-height: 1.2; } }

.download {
	padding: 1rem 0;
	text-align: center;
	background-color: #fafbfb;
	&__text {
		font-weight: 300;
		@include tr(25);
		@include css-lock(14, 16, 575, 991);
		margin-bottom: 1rem; }
	.btn-link:before {
		@include r(767) {
			display: none; } } }


.certification {
	overflow: hidden; }

.certification-slider {
	padding: 0 15px; }

.swiper-button-next,
.swiper-button-prev {
	background-image: url('data:img/png;base64,');
	width: 50px;
	height: 19px;
	background-size: contain;
	margin-top: 0;
	transiton: $transition-base;
	&.swiper-button-disabled {
		background-image: url('data:img/png;base64,');
		opacity: 1; } }

.swiper-button-prev {
	transform: rotate(-180deg); }

.certification-slider.swiper-container {
	overflow: visible; }
.certification-slider {
	.swiper-button-next,
	.swiper-button-prev {
		transform: translateY(-50%);
		@include css-lock(20, -130, 575, 1920, right);
		@include r(767) {
			width: 30px;
			height: 10px; } }
	.swiper-button-prev {
		@include css-lock(20, -130, 575, 1920, left);
		transform: translateY(-50%) rotate(-180deg); } }

.portfolio-slider {
	padding-bottom: 90px;
	position: relative;
	&__nav {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 10;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		.swiper-pagination {
			position: static;
			width: auto;
			font-weight: 700;
			color: #898d98;
			@include fs(12);
			vertical-align: top;
			&__current {
				color: $primary;
				@include fs(18);
				min-width: 24px;
				display: inline-block;
				text-align: center;
				line-height: 1;
				vertical-align: middle; }
			&__total {
				line-height: 1.5;
				display: inline-block;
				min-width: 20px; } }
		.swiper-button-next,
		.swiper-button-prev {
			position: static;
			width: 50px;
			height: 20px;
			background-size: 30px 10px; } }
	&__btns {
		margin-right: 80px;
		display: flex;
		align-items: center; } }


.portfolio-item {
	position: relative;
	&__img {
		font-size: 0; }
	&__details {
		position: absolute;
		box-shadow: 0.954px 4.908px 30px 0px rgba(0, 0, 0, 0.04);
		background-color: #fff;
		left: 55px;
		bottom: -60px;
		padding: 2rem 2.5rem;
		@include r(767) {
			padding: 1rem 1.5rem; } }
	&__details-title {
		display: block;
		font-weight: 500;
		@include css-lock(18, 22, 575, 991);
		margin-bottom: .5rem; }
	&__details-sub {
		display: block;
		color: #898d98;
		font-weight: 400;
		@include css-lock(19, 36, 575, 991); } }


.sect-form-item {
	display: flex;
	align-items: stretch;
	margin-bottom: 2rem;
	@include r(767) {
		margin-bottom: 1rem; }
	&__img {
		width: 80px;
		margin-right: 1.75rem;
		flex-shrink: 0;
		@include r(767) {
			width: 60px;
			margin-right: 1rem; } }
	&__text {
		@include tr(25);
		@include css-lock(16, 24, 575, 991);
		padding-left: 2.125rem;
		position: relative;
		display: flex;
		align-items: center;
		@include r(767) {
			padding-left: 1.5rem; }
		&:before {
			position: absolute;
			content: "";
			left: 0;
			top: 0;
			bottom: 0;
			width: 2px;
			background-color: $primary; } } }

.main-footer {
	padding: 2rem 0; }


.logo-footer {
	text-align: left;
	@include tr(25);
	margin-bottom: 1.125rem;
	&__text {
		display: block;
		color: $primary;
		white-space: nowrap;
		@include css-lock(24, 30, 575, 991);
		font-weight: 800;
		line-height: 1.2; }
	&__sub {
		display: block;
		font-weight: 600;
		@include css-lock(12, 14, 575, 991); } }

.footer-strong {
	@include css-lock(12, 14, 575, 991);
	color: rgba(0,0,0, .43);
	display: inline-block;
	line-height: 1.3; }

.footer-nav {
	margin: 0 -.5rem;
	&__item a {
		text-transform: {}
		display: inline-block;
		color: #151e2e;
		@include css-lock(14, 16, 575, 991);
		padding: .25rem 0.5rem;
		transition: $transition-base; }
	&__item a:hover {
		text-decoration: none;
		color: $primary; } }

.social {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
	margin: 0 -1rem;
	&__item {
		margin: 0 .5rem; }
	&__item a {
		display: inline-block;
		padding: .5rem; }
	&__item a .icon {
		transition: $transition-base; }
	&__item a:hover .icon {
		fill: $link-hover-color; }
	&__item .icon {
		fill: $primary; } }


.copy {
	padding: 1.25rem 0;
	&__text {
		text-align: center;
		@include css-lock(14, 16, 575, 991);
		@include tr(25); } }


.modal-form {
	background-image: url(../img/modal-form-bg.jpg);
	@include cover;
	position: relative;
	&:before {
		position: absolute;
		content: '';
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background-color: $secondary;
		opacity: .85; }
	.modal-header {
		position: relative;
		border-bottom: none;
		padding-bottom: 0; }
	.modal-header .close {
		text-shadow: none;
		display: flex;
		align-items: center;
		color: #fff;
		opacity: 1;
		font-weight: 400;
		@include css-lock(14, 18, 575, 991);
		&__icon {
			margin-left: 6px;
			line-height: 22px;
			color: $primary;
			font-size: 40px; } } }


.price-counter {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;
	padding: .25rem 0; }

.price-counter-item {
	margin-left: 1.25rem; }

.price-table {
	margin-bottom: 0;
	tr, th {
		text-align: center;
		vertical-align: middle; }
	th, td {
		vertical-align: middle;
		padding: 0.9375rem; }
	thead th {
		border-color: $primary;
		border-width: 1px;
		@include fs(18);
		font-weight: 30;
		color: #151e2e; }
	td a {
		text-decoration: underline; }
	&__caption td {
		caption-side: top;
		text-align: center;
		background-color: $primary;
		color: #fff;
		text-transform: uppercase;
		padding: 0.25rem 0;
		a {
			color: #ffffff;
			display: block;
			@include css-lock(14, 16, 575, 991);
			font-weight: 600;
			text-decoration: none; }
		a:hover {
			text-decoration: underline; } } }
