@media (min-width: 991px) { .copy__text { font-size: 16px; } }

.modal-form { background-image: url(../img/modal-form-bg.jpg); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; position: relative; }

.modal-form:before { position: absolute; content: ''; left: 0; right: 0; top: 0; bottom: 0; background-color: #192437; opacity: .85; }

.modal-form .modal-header { position: relative; border-bottom: none; padding-bottom: 0; }

.modal-form .modal-header .close { text-shadow: none; display: flex; align-items: center; color: #fff; opacity: 1; font-weight: 400; font-size: 18px; font-size: calc(14px + (18 - 14) * ((100vw - 575px) / (991 - 575))); }

@media (max-width: 575px) { .modal-form .modal-header .close { font-size: 14px; } }

@media (min-width: 991px) { .modal-form .modal-header .close { font-size: 18px; } }

.modal-form .modal-header .close__icon { margin-left: 6px; line-height: 22px; color: #d6223d; font-size: 40px; }

.price-counter { display: flex; flex-wrap: wrap; justify-content: flex-end; padding: .25rem 0; }

.price-counter-item { margin-left: 1.25rem; }

.price-table { margin-bottom: 0; }

.price-table tr, .price-table th { text-align: center; vertical-align: middle; }

.price-table th, .price-table td { vertical-align: middle; padding: 0.9375rem; }

.price-table thead th { border-color: #d6223d; border-width: 1px; font-size: 18px; font-size: 1.125rem; font-weight: 30; color: #151e2e; }

.price-table td a { text-decoration: underline; }

.price-table__caption td { caption-side: top; text-align: center; background-color: #d6223d; color: #fff; text-transform: uppercase; padding: 0.25rem 0; }

.price-table__caption td a { color: #ffffff; display: block; font-size: 16px; font-size: calc(14px + (16 - 14) * ((100vw - 575px) / (991 - 575))); font-weight: 600; text-decoration: none; }

@media (max-width: 575px) { .price-table__caption td a { font-size: 14px; } }

@media (min-width: 991px) { .price-table__caption td a { font-size: 16px; } }

.price-table__caption td a:hover { text-decoration: underline; }
